<template>
  <div class="container register-wrap">
    <img class="bg" src="../../images/register/bg.png" alt="">
    <div class="wrap-center">
       <div class="title">请选择账户类型</div>
        <div class="btn-wrap">
          <button :class="{'active': !checkStatus}" @click="checkStatus=0">
            <img :src="require(`../../images/register/company-${checkStatus ? 'off': 'on'}.png`)" alt="">
            个人账号
          </button>
           <button :class="{'active': checkStatus}"  @click="checkStatus=1">
            <img :src="require(`../../images/register/member-${!checkStatus ? 'off': 'on'}.png`)" alt="">
            企业认证
          </button>
        </div>
        <button class="next" @click="next">下一步</button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      checkStatus: 0
    }
  },
  methods:{
    next() {
      if(this.checkStatus == 0) {
        this.$router.push({name: 'personalCertification'})
      }else {
        this.$router.push({name: 'companyCertification'})
      }
    }
  }
}
</script>


<style lang="less" scoped>
.register-wrap {
  position: relative;
  .bg {
    width: 100%;
    display: block;
    min-height: calc(100vh - 66px - 100px);
  }
  .wrap-center {
    width: 594px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    background: #fff;
    padding: 50px 58px 55px 58px;
    box-sizing: border-box;
    .title {
      color: #1A1A1A;
      font-size: 16px;
      font-weight: 500;
      text-align: center;
      margin-bottom: 51px;
    }
    .btn-wrap {
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
      button {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 358px;
        height: 40px;
        border: 1px solid #E5E5E5;
        border-radius: 4px;
        flex-shrink: 0;
        background-color: #fff;
        margin-bottom: 20px;
        color: #AEAEAE;
        font-size: 14px;
        font-weight: 500;
        cursor: pointer;
        img {
          width: 20px;
          display: block;
          margin-right: 8px;
        }
      }
      .active {
        border-color: #B90909;
        color: #B90909;
      }
    }
    .next {
      width: 360px;
      height: 40px;
      line-height: 40px;
      text-align: 40px;
      background-color: #B90909;
      color: #fff;
      font-size: 14px;
      font-weight: 50px;
      border: 0;
      border-radius: 3px;
      display: block;
      margin: 40px auto 0 auto;
    }
  }
}
</style>


